@mixin barLine {
    width: 100%;
    height: 3px;
    border-radius: 100px;
    background-color: white;
}
.menu{
    position: absolute;
    display: flex;
    align-items: center;
    width: 100%;
    z-index: 10;
    &__brand{
        font-size: 2rem;
        padding: 1rem 2rem;
        color: white;
    }
    &--right{
        flex-grow: 1;
        display: flex;
        justify-content: flex-end;
    }
    nav{
        display: flex;
        font-size: 1rem;
        margin-right: 1rem;
        a{
            text-decoration: none;
            color: white;
            padding: .5rem .6rem;
            transition: .5s;
            &:hover{
                color: yellow;
            }
        }
    }
    .active{
        a{
            color: yellow;
        }
    }
    .bar{
        display: none;
        margin-right: 2rem;
    }
    &__bar{
        width: 35px;
        height: 25px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        cursor: pointer;
        .line--top{
           @include barLine();
        }
        .line--mid{
            @include barLine(); 
        }
        .line--bot{
           @include barLine();
        }
    }
    &__barClose{
        width: 35px;
        height: 25px;
        cursor: pointer;
        position: relative;
        .line--top{
           @include barLine();
           position: absolute;
           top: 41%;
        //    left: 50%;
           transform: rotate(31deg);
        //    transform: rotate(45deg)
        }
        .line--bot{
           @include barLine();
           position: absolute;
           top: 41%;
        //    left: 50%;
           transform: rotate(-31deg);
        //    transform: rotate(45deg)
        }
    }
    &--hident{
        position: absolute;
        display: flex;
        flex-direction: column;
        justify-content: center;
        background-color: rgba(0,0,0,.5);
        right: 100%;
        top: 100%;
        width: 100%;
        transition: .5s;
        z-index: 10;
        opacity: 1;
        .item{
            padding: 1rem 1rem;
            a{
                display: block;
            }
        }
       
    }
}