
.News{
    .news__content{
        .new__box{
            border-radius: 5px;
            overflow: hidden;
            margin-bottom: 2rem;
            box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
            transition: .5s;
            transform: translateY(0px);
            &:hover{
                transform: translateY(-10px);
                img{
                    transform: scale(1.1);
                }
            }
            &__img{
                width: 100%;
                height: 236px;
                overflow: hidden;
                img{
                    width: 100%;
                    height: 100%;
                    transition: .5s;
                }
            }
            &__content{
                padding: .5rem .5rem;
                &__title{
                    font-size: 1.1rem;
                    color: rgb(63, 62, 62);
                    font-weight: 400;
                    margin-bottom: 0.5rem;
                    overflow: hidden;
                    display: -webkit-box;
                    -webkit-line-clamp: 2;
                    -webkit-box-orient: vertical;
                    text-overflow: ellipsis;
                    &:hover{
                        color: black;
                    }
                }
                &__date{
                    font-weight: 500;
                    padding-left: 0.5rem;
                }
                &__sumary{
                    overflow: hidden;
                    text-align: justify;
                    display: -webkit-box;
                    -webkit-line-clamp: 3;
                    -webkit-box-orient: vertical;
                    text-overflow: ellipsis;
                }
            }
        }
    }
}