@import "../Variable.scss";
.listCandidates{
    .candidate__box{
        margin-bottom: 2rem;
        box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
        display: flex;
        flex-direction: column;
        align-items: center;
        border-radius: 5px;
        &__img{
            margin-top: 1rem;
            width: 150px;
            height: 150px;
            overflow: hidden;
            border-radius: 100%;
            img{
                width: 100%;
                height: 100%;
            }
        }
        &__name{
            font-size: 1.1rem;
            padding: .5rem 0;
            letter-spacing: 1px;
            text-transform: capitalize;
        }
        &__job{
            font-weight: bold;
            color: rgba(0,0, 0, 0.7);
        }
        &__address{
            font-weight: bold;
            color: rgba(0,0, 0, 0.5);
            margin-bottom: 1rem;
        }
        &__viewProfile{
            margin: 1rem 0;
            a{
                background-color: transparent;
                transition: .5s;
                padding: .5rem 1rem;
                border-radius: 5px;
                border: 1px solid $bgButtonApply;
                color: $bgButtonApply;
                &:hover{
                    background-color: $bgButtonApply;
                    color: white;
                }
            }
        }
    }
}