.banner{
    background: url("../../images/notebook-1971495.jpg") repeat center;
    background-size: cover;
    width: 100%;
    height: 100vh;
    overflow: hidden;
    position: relative;
   
    .banner__search{
        position: absolute;
        background-color: rgba(0,0,0,0.5);
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        &--box{
            width: 55%;
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%,-50%);
            h4{
                color: white;
                font-size: 1.5rem;
                margin-bottom: 1rem;
            }
            &--content{
                display: flex;
                justify-content: space-between;
                align-items: center;
                height: 3rem;
                background-color: white;
                border-radius: 10px;
                input:focus{
                    outline: none;
                    box-shadow: none;
                    border-color: transparent;
                }
                input{
                    border: none;
                    line-height: 3rem;
                    padding: 0rem 1rem;
                }
                button{
                    padding: 0rem 4rem;
                    height: 100%;
                    font-size: 1rem;
                    border: none;
                    border-top-right-radius: 10px;
                    border-bottom-right-radius: 10px;
                }
                button:focus{
                    outline: none;
                    box-shadow: none;
                    border-color: transparent;
                }
            }
        }
    }
}
@media (max-width:800px){
    .banner__search--box--content{
        display: grid !important;
        background-color: transparent !important;
        position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%,80%);
        input{
            margin-bottom: 1rem;
            width: 300px !important;

        }
        button{
            padding: 0.5rem 1rem !important;
            border-radius: 0px !important;
        }
    }
